import React, { Component } from 'react';
import MemeCreator from '../../components/MemeCreator/MemeCreator';
import MemeLists from '../../components/MemeLists/MemeLists';
import Ae from '@aeternity/aepp-sdk/es/ae/universal';
import ContractSource from '../../assets/MemeVotes.js';

const CONTRACT_ADDRESS = 'ct_2cMdAVT6HyxZhFsvYvojxrDuMnvuPGLhEPPq65HEcuPoUi7JDE';

class VoteEngine extends Component {

    // listOfMemes: [{ id: "gj7", name: "James", image: "https://images.pexels.com/photos/20787/pexels-photo.jpg?auto=compress&cs=tinysrgb&h=350", aettosAmount: 0, value: "" },
    //{ id: "vjj8", name: "Gift", image: "https://picsum.photos/id/237/200", aettosAmount: 2, value: "" }],
    state = {
        listOfMemes: null,
        newMemeInputs: {
            name: '',
            imageUrl: ''
        },
        client: null,
        contract_address: 'ct_2cMdAVT6HyxZhFsvYvojxrDuMnvuPGLhEPPq65HEcuPoUi7JDE'
    }

    componentDidMount() {


        //const client = this.initializeClient();
        Ae({
            url: 'https://sdk-testnet.aepps.com',
            internalUrl: 'https://sdk-testnet.aepps.com',
            networkId: 'ae_uat',
            keypair: {
                publicKey: 'ak_SmB9dDDkbs7Y4Lb11oySsyxwmxot6s7dzJaAPEefKwRbTQ551',
                secretKey: 'c7511c7de81142e633f89609c09898486cd02f6cbff7666030bdcb3c0866b7753a7eb5035c83a4a7daf4d4b03558cef548ee882a15d0131fec29382a4eb91f25'
            },
            compilerUrl: "https://compiler.aepps.com",
            nativeMode: true
        }).then(client => {
            this.setState({ client: client })
            this.callStatic(this.state.contract_address, 'getMemesLength', [])
                .then(memesLength => {

                    let newListOfMemes = [];
                    for (let i = 1; i <= memesLength; i++) {
                        this.callStatic(this.state.contract_address, 'getMeme', [i])
                            .then(meme => {
                                const newMeme = {
                                    id: ((Math.random() * 100) + i),
                                    name: meme.name,
                                    image: meme.url,
                                    aettosAmount: meme.voteCount,
                                    value: ""
                                }
                                newListOfMemes.push(newMeme);
                                console.log(newListOfMemes);
                                this.setState({ listOfMemes: newListOfMemes });
                            })

                    }

                })

        })

    }


    callStatic = (contractAddress, func, args) => {

        // const contract_address = this.state.contract_address

        return this.state.client.getContractInstance(ContractSource, { contractAddress })
            .then(contract => {
                return contract.call(func, args, { callStatic: true })
            })
            .then(calledGet => {
                return calledGet.decode()
            })
            .then(decodeGet => {
                // console.log('decodeGet', decodeGet);
                return decodeGet
            })

    };

    contractCall = (contractAddress, func, args, value) => {

        //const contract_address = this.state.contract_address

        return this.state.client.getContractInstance(ContractSource, { contractAddress })
            .then(contract => {
                return contract.call(func, args, { amount: value })
            })
            .then(calledSet => {
                return calledSet
            })
    };



    voteHandler = (memeId) => {

        this.setState((prevState, props) => {

            const newListOfMemes = [
                ...prevState.listOfMemes
            ]

            const memeIndex = newListOfMemes.findIndex(({ id }) => id === memeId);

            let newMeme = {
                ...newListOfMemes[memeIndex]
            }

            newMeme.aettosAmount = prevState.listOfMemes[memeIndex].aettosAmount + prevState.listOfMemes[memeIndex].value;

            newListOfMemes[memeIndex] = newMeme;

            this.contractCall(this.state.contract_address, 'voteMeme', [memeIndex+1], prevState.listOfMemes[memeIndex].value)
                .then(res => {
                    console.log(res)
                    this.setState({ listOfMemes: newListOfMemes })      
                })
                .catch(err => console.log(err))

        });
    };

    onChangeHandler = (memeId, event) => {

        const newListOfMemes = [
            ...this.state.listOfMemes
        ]

        const memeIndex = newListOfMemes.findIndex(({ id }) => id === memeId);

        let newMeme = {
            ...newListOfMemes[memeIndex]
        }

        newMeme.value = +event.target.value;

        newListOfMemes[memeIndex] = newMeme;

        this.setState({ listOfMemes: newListOfMemes });

    };

    newNameHandler = (event) => {

        const updatedInputs = {
            ...this.state.newMemeInputs
        }
        updatedInputs.name = event.target.value;

        this.setState({ newMemeInputs: updatedInputs });
    };

    newUrlHandler = (event) => {

        const updatedInputs = {
            ...this.state.newMemeInputs
        }
        updatedInputs.imageUrl = event.target.value;

        this.setState({ newMemeInputs: updatedInputs });
    };

    createMemeHandler = () => {

        const imgUrl = this.state.newMemeInputs.imageUrl;
        const name = this.state.newMemeInputs.name;

        if (!imgUrl && !name) {
            return;
        }

        const newListOfMemes = [
            ...this.state.listOfMemes
        ];

        const meme = {
            id: this.state.newMemeInputs.name + ((Math.random() * 100) + 1),
            name: this.state.newMemeInputs.name,
            image: this.state.newMemeInputs.imageUrl,
            aettosAmount: 0,
            value: ""
        }

        newListOfMemes.push(meme);

        this.contractCall(this.state.contract_address, 'setMeme', [imgUrl, name], 0)
            .then(res => {
                console.log(res)
                this.setState({ listOfMemes: newListOfMemes });
            })
            .catch(err => console.log(err))

    };

    render() {

        return (
            <div>
                <MemeCreator
                    value={this.state.newMemeInputs}
                    createMemeClicked={this.createMemeHandler}
                    urlChanged={this.newUrlHandler}
                    nameChanged={this.newNameHandler} />
                {this.state.listOfMemes ?
                    <MemeLists
                        listOfMemes={this.state.listOfMemes}
                        inputChanged={this.onChangeHandler}
                        voteClicked={this.voteHandler} /> : null}

            </div>
        );
    }
}

export default VoteEngine;